body {
  font-family: "Roboto", sans-serif;
}

h1,
p,
.user-name {
  text-align: center;
}

table {
  border-collapse: collapse;
  margin: 0 auto;
}

th {
  background-color: #ddd;
}

th,
td {
  border: 1px solid black;
  padding: 10px;
}

.completed {
  background-color: lime;
}

.not-completed {
  background-color: red;
}
